<template>
	<view class="wrap">
		<view class="dev-area">
			<view class="dev-card">
				<view class="">
					<view class="dev-name">温度</view>
					<image class="dev-log" src="../../static/temprature.png" mode=""></image>
				</view>
				<view class="dev-data">{{temp}}℃</view>
			</view>
			<view class="dev-card">
				<view class="">
					<view class="dev-name">湿度</view>
					<image class="dev-log" src="../../static/humidity.png" mode=""></image>
				</view>
				<view class="dev-data">{{humi}}%</view>
			</view>
			<view class="dev-card">
				<view class="">
					<view class="dev-name">台灯</view>
					<image class="dev-log" src="../../static/lamp.png" mode=""></image>
				</view>
				<switch :checked="led" @change="onLedSwitch" color="#1afa29" />
			</view>
			<view class="dev-card">
				功能拓展
			</view>
		</view>
	</view>

</template>

<script>
	const {
		createCommonToken
	} = require('@/key.js')
	export default {
		data() {
			return {
				temp: '28',
				humi: '68',
				led0: true,
				token: '',
			}
		},
		onLoad() {
			const params = {
				author_key: 'Y3+MsBWLrV2rY9RYfFCScqSU2EnwYXkqNWYVD1yAbMmWbK99XqHVTy6xkR0eK9g+hdMGHvpFLXeUHSkoHKEmiw==',
				version: '2022-05-01',
				user_id: '231258',
			}
			this.token = createCommonToken(params);
			// console.log(this.token);
		},
		onShow() {
			// this.getDataFromOnenet();
			setInterval(() => { //这样可以不断更新APP里面的数据
				this.getDataFromOnenet();
			}, 3000)
		},
		methods: {
			getDataFromOnenet() {
				uni.request({
					url: 'https://iot-api.heclouds.com/thingmodel/query-device-property', //仅为示例，并非真实接口地址。
					method: 'GET',
					data: {
						product_id: 'DuoX1d1Wan',
						device_name: 'D001'
					},
					header: {
						'authorization': this.token //自定义请求头信息
					},
					success: (res) => {
						console.log('humidity:' + res.data.data[0].value);
						console.log('led0:' + res.data.data[1].value ? 'ON' : 'OFF');
						console.log('temprature:' + res.data.data[2].value);
						this.temp = res.data.data[2].value;
						this.humi = res.data.data[0].value;
						this.led0 = res.data.data[1].value == "true" ? true : false;
					}
				});
			},
			onLedSwitch(event) {
				console.log(event.detail.value);
				let value = event.detail.value;
				uni.request({
					url: 'https://iot-api.heclouds.com/thingmodel/set-device-property', //仅为示例，并非真实接口地址。
					method: 'POST',
					data: {
						product_id: 'DuoX1d1Wan',
						device_name: 'D001',
						params: {
							"led0": value
						}
					},
					header: {
						'authorization': this.token //自定义请求头信息
					},
					success: () => {
						console.log();

					}
				});
			}

		}
	}
</script>

<style>
	.wrap {
		padding: 30rpx;
	}
	.dev-area {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.dev-card {
		height: 200rpx;
		width: 320rpx;
		border-radius: 30rpx;
		margin-top: 30rpx;
		display: flex;
		justify-content:space-around;
		align-items: center;
		box-shadow: 0 0 15rpx #ccc;
	}
	.dev-name {
		font-size: 20rpx;
		text-align: center;
		color: #6d6d6d;
	}
	.dev-log {
		height: 70rpx;
		width: 70rpx;
		margin-top: 10rpx;
	}
	.dev-data {
		font-size: 50rpx;
		color: #6d6d6d;
	}
</style>